@import '../../../globalStyles/colors.scss';

@mixin shadowColor($color) {
  @media (hover: hover) {
    box-shadow: 0 0 0.71rem $color;
  }
}

.buttonsContainer {
  position: absolute;
  top: -1.33rem;
  right: 1.07rem;

  & button:first-child {
    margin-right: 0.35rem;
  }

  &.blue button:not(:disabled):hover {
    @include shadowColor($blue);
  }

  &.green button:not(:disabled):hover {
    @include shadowColor($green);
  }

  &.purple button:not(:disabled):hover {
    @include shadowColor($purple);
  }

  &.red button:not(:disabled):hover {
    @include shadowColor($red);
  }

  &.orange button:not(:disabled):hover {
    @include shadowColor($orange);
  }

  &.yellow button:not(:disabled):hover {
    @include shadowColor($yellow);
  }
}
