.arrowLink {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: right;
  width: 100%;
  height: 100%;

  font-size: 3rem;
  transition: all 0.2s linear;

  //margin-left: auto;
  padding-right: 1rem;

  &:active {
    span {
      transform: translateX(15%);
    }
  }

  span {
    position: relative;
    //left: -0.93rem;
    top: -0.42rem;
  }
}
