.list {
  position: relative;
  padding: 0 0.71rem;
  margin-bottom: 2.85rem;

  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: overlay; // скролл не смещает контент

  &::before,
  &::after {
    position: sticky; // прилипает к границе родителя
    z-index: 2;
    display: block;
    width: 100%;
    height: 4.28rem;

    margin-top: -1rem;
    content: '';
    pointer-events: none;
  }

  &::before {
    top: -1px;
  }
  &::after {
    bottom: -1px;
  }

  &.scrolled {
    &::before {
      background: linear-gradient(0deg, transparent 0%, white 100%);
    }
    &::after {
      background: linear-gradient(0deg, white 0%, transparent 100%);
    }
  }
}

.list::-webkit-scrollbar {
  width: 0.71rem;
  border-radius: 0.35rem;
}

.list::-webkit-scrollbar-thumb {
  background-color: gray;
  border-radius: 0.35rem;
}

.list::-webkit-scrollbar-track {
  background-color: #ccc;
  border-radius: 0.35rem;
}

.emptyList {
  width: 100%;
  text-align: center;
  position: absolute;
  padding-right: 1.42rem;
  top: 0;

  font-size: 1.5rem;
}
