@import '../../../globalStyles/colors.scss';

@mixin color($color) {
  border-color: $color;
  .checkCenter {
    background: $color;
  }
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 8%;
}

.checkBorder {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 1.8rem;
  height: 1.8rem;

  border-radius: 50%;
  border: 0.1rem solid #ccc;
  //transition: border 0.1s linear;

  //margin: auto 1.8rem auto auto;

  cursor: pointer;

  &.green {
    @include color($green);
  }

  &.blue {
    @include color($blue);
  }

  &.purple {
    @include color($purple);
  }

  &.red {
    @include color($red);
  }

  &.orange {
    @include color($orange);
  }

  &.yellow {
    @include color($yellow);
  }

  .checkCenter {
    width: 80%;
    height: 80%;
    border-radius: 50%;
    opacity: 0;
    //transition: 0.1s linear;
    &.checked {
      opacity: 1;
    }
  }
}
