$blue: #5b8bdc;

.button {
  $sizeBtn: 2.7rem;
  position: relative;
  width: $sizeBtn;
  height: $sizeBtn;
  overflow: hidden;

  border: none;
  background: #ffffff;
  box-shadow: 0 0 0.21rem #ccc;

  font-size: 1.3rem;
  line-height: 1.5rem;

  transition: all 0.1s linear;

  cursor: pointer;

  &.disabled {
    cursor: no-drop;
    img {
      opacity: 0.3;
    }

    &:hover {
      box-shadow: 0 0 0.21rem #ccc;
    }
    &:active {
      scale: 1;
    }
  }

  &:active {
    scale: 0.9;

    box-shadow: 0 0 0.35rem black;
  }

  img {
    position: absolute;
    top: 17.5%;
    left: 17.5%;

    width: 65%;
    height: 65%;
    object-fit: cover;

    background: transparent;
  }
}

.backBtn {
  position: relative;
  top: 1rem;
  width: 5rem;
  height: 2.14rem;

  box-shadow: none;

  color: $blue;
  font-weight: bold;
  text-align: right;

  &:hover {
    color: darken($color: $blue, $amount: 20%);

    &::before {
      border-left-color: darken($color: $blue, $amount: 20%);
      border-bottom-color: darken($color: $blue, $amount: 20%);
    }
  }

  &:active {
    box-shadow: none;
  }

  &::before {
    display: block;
    position: absolute;
    left: 0.35rem;

    width: 1.42rem;
    height: 1.42rem;

    border: 0.21rem solid transparent;
    border-left-color: #5b8bdc;
    border-bottom-color: #5b8bdc;

    transform: rotate(45deg);
    content: '';
  }
}

.itemBtn {
  border-radius: 50%;
}

.addItemBtn {
  $sizeBtn: 3.57rem;
  border-radius: 50%;
  width: $sizeBtn;
  height: $sizeBtn;
  background: $blue;

  &:hover {
    @media (hover: hover) {
      background: darken($color: $blue, $amount: 20%);
    }
  }

  span {
    left: 50%;

    &::before,
    &::after {
      $sizePseudoEl: 1.65rem;

      display: block;
      position: absolute;
      left: calc(($sizeBtn / 2) - ($sizePseudoEl / 2));
      width: $sizePseudoEl;
      height: 0.1rem;
      background: white;
      content: '';
    }
    &::after {
      transform: rotate(90deg);
    }
  }
}

.closeBtn {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  width: 1.7rem;
  height: 1.5rem;
  background: transparent;
  transition: 0.1s linear;
  box-shadow: none;

  &::after,
  &::before {
    position: absolute;
    display: block;
    width: 100%;
    height: 0.14rem;
    background: black;
    content: '';
  }

  &:active,
  &:hover {
    box-shadow: none;
  }

  &::after {
    transform: rotate(45deg);
  }

  &::before {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    scale: 0.8;
  }

  &:hover::before,
  &:hover::after {
    background: red;
  }
}

.todoBtn {
  border-radius: 0.5rem;
  &:hover {
    box-shadow: 0 0 0.21rem red;
  }
}
