@import '../../globalStyles/colors.scss';

@mixin colorStyle($color) {
  border-left-color: $color;
  &:hover {
    @media (hover: hover) {
      box-shadow: 0 0.21rem 0.71rem $color;
    }
  }

  & a {
    color: $color;
    &:hover {
      @media (hover: hover) {
        color: darken($color: $color, $amount: 20%);
      }
    }
  }
}

.container {
  position: relative;
  display: flex;
  justify-content: space-between;

  box-shadow: 0 0.21rem 0.71rem #ccc;
  border-radius: 0.5rem;
  border-left: 0.5rem solid;

  margin-bottom: 2.2rem;
  margin-right: 0.71rem;

  padding: 2rem 1rem;

  text-align: left;
  font-size: 1.4rem;

  &:hover {
    @media (hover: hover) {
      transform: scale(1.01);
    }
  }

  &.checked {
    border-left-color: #ccc;
    &:hover {
      transform: scale(1);
      box-shadow: 0 0.21rem 0.71rem #ccc;
    }

    span {
      font-style: italic;
      text-decoration: line-through;
      pointer-events: none;
    }
  }

  a {
    text-decoration: none;
  }

  .textInput {
    width: 100%;
    height: 2rem;

    font-size: inherit;

    border-bottom: 0.07rem solid black;
  }

  .children {
    margin-left: auto;
    margin-right: 0.71rem;
  }
}

.todoText {
  width: 87%;
  line-height: 2rem;
  word-wrap: break-word;
  &.checked {
    font-style: italic;
    text-decoration: line-through;
  }
}

.green {
  @include colorStyle($green);
}

.blue {
  @include colorStyle($blue);
}

.purple {
  @include colorStyle($purple);
}

.red {
  @include colorStyle($red);
}

.orange {
  @include colorStyle($orange);
}

.yellow {
  @include colorStyle($yellow);
}

.show {
  animation: show 0.5s;
}

.hide {
  animation: hide 0.5s;
}

@keyframes show {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes hide {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100%);
  }
}
