@import '../../../globalStyles/colors.scss';

@mixin color($color) {
  textarea {
    border: 0.1rem solid $color;
  }

  .addBtn {
    background-color: $color;
  }
}

.form {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;

  background: whitesmoke;
  border-radius: 0.71rem;
  padding: 3rem 2rem;
  box-shadow: 0 0 0.35rem #ccc;

  &.green {
    @include color($green);
  }

  &.blue {
    @include color($blue);
  }

  &.purple {
    @include color($purple);
  }

  &.red {
    @include color($red);
  }

  &.orange {
    @include color($orange);
  }

  &.yellow {
    @include color($yellow);
  }

  &.error {
    @include color(red);
  }

  .textError {
    position: absolute;
    top: 0.7rem;
    left: 0;

    width: 100%;

    text-align: center;
    font-weight: bold;
    font-size: 1.8rem;
    color: red;
  }

  textarea {
    resize: none;
    font-size: 1.7rem;
    padding: 0.4rem;
    border-radius: 0.71rem;

    margin-bottom: 0.71rem;
  }

  .closeBtn {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    width: 1.7rem;
    height: 1.5rem;
    background: transparent;
    transition: 0.1s linear;

    &::after,
    &::before {
      position: absolute;
      display: block;
      width: 100%;
      height: 0.14rem;
      background: black;
      content: '';
    }

    &::after {
      transform: rotate(45deg);
    }

    &::before {
      transform: rotate(-45deg);
    }

    &:active::before,
    &:active::after {
      scale: 0.8;
    }

    &:hover::before,
    &:hover::after {
      background: red;
    }
  }

  .addBtn {
    background: white;
    border-radius: 0.71rem;
    font-weight: bolder;
    font-size: 1.5rem;
    padding: 0.7rem;
    &:active {
      transform: scaleX(0.99) scaleY(0.99);
    }
  }
}
